import React, { Component } from 'react'
import { Form, DatePicker } from 'antd'
import { generateStyle } from '../util'
import moment from 'moment'
import { postAction } from '@/axios';
const FormItem = Form.Item
class BaseDate extends Component {
    componentDidUpdate(preProps){
        if (JSON.stringify(preProps) !== JSON.stringify(this.props)) {
            // 重置所有表单项 ，方便更新数据
            this.props.form.resetFields()
        }
    }  
    handleChange = () => {
        // 检查关联
        let hasAss = this.fun_checkAssociattion(this.props.item.common.onlyId, this.props.item.id)
        if (hasAss) {
            this.getBindAssociation()
        }
    }
    /**
    |--------------------------------------------------
    | 根据params拿到想要的参数
    |--------------------------------------------------
    */
    fun_combineParam = (type) => {
        let params = this.props.item.common[type] || []
        let list = this.props.list, obj = {}
        params.forEach(item => {
            if (item.indexOf('.') > 0) {
                // 参数关联的是表格列或者是值字段
                if(item.indexOf('BindDataValue')>=0){
                    let index = list.findIndex(inner => inner.common.onlyId ===  item.split('.')[0])
                    if (index >= 0) {
                        obj[item] = list[index].data.BindDataValue || ''
                    }
                }
            } else {
                let index = list.findIndex(inner => inner.common.onlyId === item)
                if (index >= 0) {
                    obj[item] = list[index].actualValue
                }
            }
        })
        return obj
    }
    /**
        |--------------------------------------------------
        | 获取焦点的时候去拿到当前组件联想或者关联的时候所需要的参数
        |--------------------------------------------------
        */
    handleFocus = () => {
        // 检查关联
        let hasAss = this.fun_checkAssociattion(this.props.item.common.onlyId, this.props.item.id)
        if (hasAss) {
            let param = {
                tableWidgetId: '',
                widgetId: this.props.item.common.onlyId,
                formId: this.props.id,
                fillingMode: 'AssociationFilling'
            }
            postAction(`/ybt/form/preview/need/widget/dataParams`, param, 1, 1).then(res => {
                if (res.success) {
                    if (JSON.stringify(res.obj) !== '{}' && JSON.stringify(res.obj) !== '[]') {
                        this.props.handleUpdateOption(res.obj, this.props.item.id, 'ass_params')
                    }
                }
            })
        }
    }
    // 得到关联填充的数据
    getBindAssociation = () => {
        let params = this.fun_combineParam('ass_params')
        postAction(`/ybt/form/preview/associationFill/data?formId=${this.props.id}&tableWidgetId=&widgetId=${this.props.item.common.onlyId}`, params).then(res => {
            if (res.success) {
                // 将取到的数据推送到父级的list
                this.props.sendAssociationData(res.obj)
            }
        })
    }
    // 检测是否有其他任意组件和当前组件关联,首先要过滤掉自身
    fun_checkAssociattion = (onlyId, compId) => {
        let tag = false, list = this.props.list
        list = list.filter(item => item.id !== compId)
        list.forEach(item => {
            if (item.children) {
                if (item.children.length > 0) {
                    let index = item.children.findIndex(i => onlyId === i.data.AssociatedControlsID)
                    if (index >= 0) {
                        tag = true
                    }
                }
            }
            if (item.data && onlyId === item.data.AssociatedControlsID) {
                tag = true
            }
        })
        return tag
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        let item = this.props.item
        const formItemLayout = {
            labelCol: {
                span: 4
            },
            wrapperCol: {
                span: 20
            },
        };
        let style = generateStyle(this.props.item.style) || {}
        return (
            <Form {...formItemLayout}>
                <FormItem label={item.common.label}>
                    {
                        getFieldDecorator('actualValue', {
                            initialValue: !item.actualValue || item.actualValue === '' ? null : moment(item.actualValue),
                            
                        })(<DatePicker
                            onChange={this.handleChange}
                            style={style}
                            onFocus={this.handleFocus}
                            disabled={item.common.ReadOnly ? true : false}
                        />)
                    }
                </FormItem>
            </Form>
        )
    }
}
BaseDate = Form.create({
    onValuesChange: (props, changedFields, allFields) => {
        props.handlePropChange(props.item, changedFields)
    }
})(BaseDate)
export default BaseDate
